// Ïò§ÌîºÏä§ Í≥µÏã§Î•† Îç∞Ïù¥ÌÑ∞ (Ï£ºÏöî ÏóÖÎ¨¥ÏßÄÍµ¨)
export const generateOfficeVacancyData = () => {
    const districts = [
        { id: 'cbd', name: 'CBD (Í¥ëÌôîÎ¨∏)', baseVacancy: 8 },
        { id: 'gbd', name: 'GBD (Í∞ïÎÇ®)', baseVacancy: 5 },
        { id: 'ybd', name: 'YBD (Ïó¨ÏùòÎèÑ)', baseVacancy: 10 },
        { id: 'pangyo', name: 'ÌåêÍµê', baseVacancy: 4 },
        { id: 'magok', name: 'ÎßàÍ≥°', baseVacancy: 12 },
    ];

    return districts.map(district => ({
        ...district,
        vacancy: parseFloat((district.baseVacancy + (Math.random() - 0.5) * 3).toFixed(1)),
        avgRent: Math.round(80000 + Math.random() * 40000), // Ïõê/„é°/Ïõî
        change: parseFloat(((Math.random() - 0.5) * 2).toFixed(1)),
        primeRent: Math.round(120000 + Math.random() * 30000),
    }));
};

// Ïò§ÌîºÏä§ Í≥µÏã§Î•† Ï∂îÏù¥
export const generateOfficeVacancyTrend = () => {
    const data = [];

    for (let year = 2018; year <= 2025; year++) {
        for (let quarter = 1; quarter <= 4; quarter++) {
            if (year === 2025 && quarter > 4) break;

            let cbd, gbd, ybd;

            if (year <= 2019) {
                cbd = 9 + Math.random() * 2;
                gbd = 4 + Math.random();
                ybd = 11 + Math.random() * 2;
            } else if (year === 2020) {
                cbd = 12 + Math.random() * 2; // ÏΩîÎ°úÎÇò
                gbd = 6 + Math.random() * 2;
                ybd = 14 + Math.random() * 2;
            } else if (year <= 2022) {
                cbd = 10 + Math.random() * 2;
                gbd = 5 + Math.random();
                ybd = 12 + Math.random() * 2;
            } else {
                cbd = 8 + Math.random() * 2;
                gbd = 4 + Math.random();
                ybd = 10 + Math.random() * 2;
            }

            data.push({
                period: `${year} Q${quarter}`,
                year,
                quarter,
                cbd: parseFloat(cbd.toFixed(1)),
                gbd: parseFloat(gbd.toFixed(1)),
                ybd: parseFloat(ybd.toFixed(1)),
            });
        }
    }

    return data;
};

// ÏÉÅÍ∞Ä Í∂åÎ¶¨Í∏à Îç∞Ïù¥ÌÑ∞ (ÏÑúÏö∏ Ï£ºÏöî ÏÉÅÍ∂å)
export const generateRetailPremiumData = () => {
    const districts = [
        { name: 'Í∞ïÎÇ®Ïó≠', premium: 85000, rent: 450000 },
        { name: 'ÌôçÎåÄÏûÖÍµ¨', premium: 72000, rent: 380000 },
        { name: 'Ïù¥ÌÉúÏõê', premium: 35000, rent: 280000 },
        { name: 'Î™ÖÎèô', premium: 95000, rent: 520000 },
        { name: 'Ïã†Ï¥å', premium: 45000, rent: 320000 },
        { name: 'Ïû†Ïã§', premium: 55000, rent: 350000 },
        { name: 'Í∞ÄÎ°úÏàòÍ∏∏', premium: 68000, rent: 420000 },
        { name: 'ÏÑ±ÏàòÎèô', premium: 62000, rent: 380000 },
    ];

    return districts.map(item => ({
        ...item,
        premium: item.premium + Math.round((Math.random() - 0.5) * 10000),
        rent: item.rent + Math.round((Math.random() - 0.5) * 50000),
        vacancyRate: parseFloat((3 + Math.random() * 5).toFixed(1)),
        trend: Math.random() > 0.5 ? 'up' : 'down',
    }));
};

// Î¨ºÎ•òÏÑºÌÑ∞ Îç∞Ïù¥ÌÑ∞
export const generateLogisticsData = () => {
    const data = [];

    for (let year = 2018; year <= 2025; year++) {
        let supply = 200 + (year - 2018) * 50;
        let demand = 180 + (year - 2018) * 60;

        if (year >= 2020) {
            supply += 100; // Ïù¥Ïª§Î®∏Ïä§ ÏÑ±Ïû•
            demand += 150;
        }

        if (year >= 2023) {
            supply += 50;
            demand -= 20; // ÎëîÌôî
        }

        data.push({
            year,
            supply: Math.round(supply + (Math.random() - 0.5) * 30), // ÎßåÌèâ
            demand: Math.round(demand + (Math.random() - 0.5) * 30),
            vacancy: parseFloat((5 + (supply - demand) / 50).toFixed(1)),
            avgRent: Math.round(30000 + (year - 2018) * 2000 + Math.random() * 3000), // Ïõê/Ìèâ/Ïõî
        });
    }

    return data;
};

// Îç∞Ïù¥ÌÑ∞ÏÑºÌÑ∞ ÏãúÏû• Îç∞Ïù¥ÌÑ∞
export const generateDataCenterData = () => {
    const data = [];

    for (let year = 2020; year <= 2028; year++) {
        const isProjection = year > 2025;
        let capacity = 500 + (year - 2020) * 100;

        if (year >= 2023) capacity += 200; // ÌÅ¥ÎùºÏö∞Îìú ÏàòÏöî Ï¶ùÍ∞Ä

        data.push({
            year,
            capacity: Math.round(capacity + (Math.random() - 0.5) * 50), // MW
            growth: parseFloat((15 + Math.random() * 10).toFixed(1)), // %
            utilization: parseFloat((85 + Math.random() * 10).toFixed(1)), // %
            isProjection,
        });
    }

    return data;
};

// Ìò∏ÌÖî/ÏàôÎ∞ïÏóÖ ÏßÄÌëú
export const generateHotelData = () => {
    const data = [];

    for (let year = 2018; year <= 2025; year++) {
        let occupancy, adr; // Average Daily Rate

        if (year <= 2019) {
            occupancy = 75 + Math.random() * 5;
            adr = 150000 + Math.random() * 20000;
        } else if (year === 2020) {
            occupancy = 30 + Math.random() * 10; // ÏΩîÎ°úÎÇò
            adr = 100000 + Math.random() * 20000;
        } else if (year === 2021) {
            occupancy = 45 + Math.random() * 10;
            adr = 120000 + Math.random() * 20000;
        } else if (year === 2022) {
            occupancy = 60 + Math.random() * 10;
            adr = 140000 + Math.random() * 20000;
        } else {
            occupancy = 75 + Math.random() * 8;
            adr = 170000 + Math.random() * 30000;
        }

        data.push({
            year,
            occupancy: parseFloat(occupancy.toFixed(1)), // %
            adr: Math.round(adr), // Ïõê
            revpar: Math.round(adr * (occupancy / 100)), // Revenue Per Available Room
        });
    }

    return data;
};

// ÏÉÅÏóÖÏö© Î∂ÄÎèôÏÇ∞ ÏöîÏïΩ
